.App {
   display: flex;
   align-items: center;
   font-family: sans-serif;
   font-size: 25px;
   flex-direction: column;
}
body {
  margin: 0;


}


.Header {
  background-color: #00CECE;
  width: 100%;
  height: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;

}
.Header input {
  width: 200px;
  height: 40px;
  border-radius: 10px;
  border: 2px solid hotpink;
  font-size: 20px;
  padding-left: 10px;
}

.Header button {
  margin-top: 10px;
  width: 150px;
  height: 60px;
  border-radius: 10px;
  background-color: darkblue;
  color: white;
  font-size: 15px;
  padding-left: 10px;
  border: none;
}

.Header button:hover {
  cursor: pointer;
}

.DisplaySection {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #007A7A;
  color: white;
  text-transform: capitalize;
  border: solid black 5px;
  border-radius: 50px;
  width: 500px;
}

.DisplaySection h3, h4 {
  margin:  5px;

}

.DisplaySection img {
  width: 300px;
  margin: 0;
}